.newsletter-section{
    width: 100%;
    height: 400px;
    display: flex;
    align-items: center;
    background: url('../../../assets/newsletter-bg.jpeg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.newsletter-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}
.small-text{
    margin-bottom: 15px;
    text-transform: uppercase;
    color: rgba(0, 0, 0, .5);
}
.big-text{
    margin-bottom: 20px;
    font-size: 28px;
    line-height: 30px;
    font-weight: 500;
    text-transform: uppercase;
    padding: 0 30px;
}
.form{
    display: flex;
    gap: 5px;
    margin-bottom: 10px;
}
input{
    width: 300px;
    height: 40px;
    border-radius: 0;
    border: 1px solid rgba(0, 0, 0, .2);
    padding: 0 12px;
    font-size: 16px;
    outline: none;
}
button{
    outline: none;
    border: none;
    height: 40px;
    width: 120px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 16px;
    color: white;
    background-color: #8e2de2;
    border-bottom: 3px solid #8e2de2;
}
.text{
    margin-bottom: 20px;
    font-size: 14px;
    color: rgba(0, 0, 0, .5);
}
.social-icons{
    display: flex;
    gap: 10px;
    align-items: center;
}
.icons{
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, .8);
    border-radius: 50%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}









@media screen and (max-width: 748px) and (min-width: 300px){

.big-text{
   font-size: 15px;
}

input{
    width: 200px;
}
button{
    width: 100px;
}

}